import "./App.css";
import "antd-mobile/dist/antd-mobile.css";
import { WingBlank, TextareaItem, Button, Card } from "antd-mobile";
import { useState } from "react";
import { parse } from "url";

function App() {
  const [originString, setOriginString] = useState("");
  const [convertedString, setConvertedString] = useState("");

  function convert() {
    const id = parse(originString, true).query.id;
    setConvertedString(
      `https://h5.m.taobao.com/cart/order.html?buyParam=${id}_1`
    );
  }
  return (
    <div className="App">
      <WingBlank>
        <TextareaItem
          autoHeight
          value={originString}
          onChange={(e) => setOriginString(e)}
          placeholder="请输入需要转换的链接"
          rows={10}
        ></TextareaItem>
      </WingBlank>
      <br />
      <WingBlank>
        <Button type="primary" onClick={convert}>
          转换
        </Button>
      </WingBlank>
      <br />
      <WingBlank>
        <Card>
          <Card.Header title="转换结果" />
          <Card.Body>
            <div>
              <span>{convertedString}</span>
            </div>
            <a target="_blank" rel="noreferrer" href={convertedString}>
              点我点我点我
            </a>
          </Card.Body>
        </Card>
      </WingBlank>
    </div>
  );
}

export default App;
